import "./Card.css";
import { useState } from "react";

import { useDispatch } from "react-redux";
import { selectCard, deselectCard } from "../../redux/slices/GameStateSlice";

import HelpIcon from "./../../assets/question-mark.svg";
import { motion } from "framer-motion";
import { CardFactory } from "./CardFactory";

interface ICard {
  cardName: string;
  cardBorderColour: string;
  cardImage: any;
  cardIcon: any;
  cardFlavourText: string | null;
  cardEffectText: string | null;
}

interface ICardFactory {
  id: string;
  type: string;
  index?: number;
  onHover?: any;
  hoveredIndex?: number;
  margin?: number;
}

const Card = (props: ICard) => {
  return (
      <div
        className="card card-front"
        style={{ outline: `3px solid ${props.cardBorderColour}` }}
      >
        <img className="card-help-icon" src={HelpIcon} alt="HelpIcon" />
        <div className="card-top card-banner">
          <img
            className="card-icon"
            src={props.cardIcon}
            alt={props.cardName}
          />
          <h1 className="card-name">{props.cardName}</h1>
        </div>
        <div className="card-middle">
          <img
            src={props.cardImage}
            alt=""
            className="card-image"
            draggable="false"
          />
        </div>
        <div className="card-bottom card-banner">
          <img
            className="card-icon"
            src={props.cardIcon}
            alt={props.cardName}
          />
          <h1 className="card-name">{props.cardName}</h1>
        </div>
      </div>
  );
};


const offsetDuration = 0.35;

const InHandCardVariant = {
  enter: {
    opacity: 0,
    x: 0,
    y: -100,
    rotateZ: 0
  },

  onMount: {
    opacity: 1,
    x: 0,
    y: 0,
    rotateZ: 0,
    transition: {
      y: {
        type: "tween", duration: 0.25
      },
      type: "tween",
      duration: offsetDuration,
    },
  },

  unselected: {
    opacity: 1,
    x: 0,
    y: 0,
    rotateZ: 0,
    transition: {
      y: {
        type: "spring", duration: 0.15
      },
      type: "tween",
      duration: offsetDuration,
    },
  },

  selected: {
    x: 0,
    y: -50,
    rotateZ: 0,
    opacity: 1,
    transition: {
      y: {
        type: "spring", duration: 0.15
      },
      type: "tween",
      duration: offsetDuration,
    },
  },
  hoverRightSelected: (margin:number) => {
    return {
      x: margin,
      y: -50,
      rotateZ: 0,
      opacity: 1,
      transition: {
        y: {
          type: "spring", duration: 0.15
        },
        type: "tween",
        duration: offsetDuration,
      }
    }
  },
  hoverRightUnselected: (margin:number) => {
    return {
      x: margin,
      y: 0,
      rotateZ: 0,
      opacity: 1,
      transition: {
        y: {
          type: "spring", duration: 0.15
        },
        type: "tween",
        duration: offsetDuration,
      }
    }
  },
};

const InHandCard = (props: ICardFactory): JSX.Element => {
  const [variant, setVariant] = useState("onMount");

  const dispatch = useDispatch();

  const handleClick = () => {
    if (variant === "unselected" || variant ===  "onMount") { 
      setVariant("selected");
      dispatch(selectCard(props.id));
    } else {
      setVariant("unselected");
      dispatch(deselectCard(props.id));
    }
  };

  return (
    <motion.li
      key={props.id}
      custom={props.margin}
      layoutId={props.id}
      className="in-hand-card"
      initial="enter"
      animate={props.index > props.hoveredIndex ? (variant === "selected" ? 'hoverRightSelected' : 'hoverRightUnselected') : variant}
      variants={InHandCardVariant}
      onClick={handleClick}
      whileHover={{ scale: 1.1 }}
      onHoverStart={()=>props.onHover(props.index)}
      onHoverEnd={()=>props.onHover(Infinity)}
    >
      {CardFactory(props.id, props.type)}
    </motion.li>
  );
};

const offsetTranslateScale = 30;
const maxRotation = 10;
const OnBoardCard = (props: ICardFactory) => {
  const [offsets, _] = useState({
    x: Math.random() * offsetTranslateScale - offsetTranslateScale / 2,
    y: Math.random() * offsetTranslateScale - offsetTranslateScale / 2,
    rot: Math.random() * maxRotation - maxRotation / 2,
  });

  return (
    <motion.div
      layoutId={props.id}
      className="card-played"
      initial={{
        x: offsets.x,
        y: offsets.y,
      }}
      animate={{
        x: offsets.x,
        y: offsets.y,
      }}
    >
      {CardFactory(props.id, props.type)}
    </motion.div>
  );
};

export { Card, InHandCard, OnBoardCard };
