import { createSlice } from "@reduxjs/toolkit";
import { RootState } from "../Store";

export const GameStateSlice = createSlice({
  name: "gameState",
  initialState: {
    myCards: [{id: `${Math.random() * 100000000}`, type: 'nope'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'},{id: `${Math.random() * 100000000}`, type: 'nope'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'},{id: `${Math.random() * 100000000}`, type: 'nope'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'},{id: `${Math.random() * 100000000}`, type: 'nope'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'},{id: `${Math.random() * 100000000}`, type: 'nope'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'},{id: `${Math.random() * 100000000}`, type: 'nope'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'},{id: `${Math.random() * 100000000}`, type: 'nope'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'},{id: `${Math.random() * 100000000}`, type: 'nope'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'},{id: `${Math.random() * 100000000}`, type: 'nope'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'},{id: `${Math.random() * 100000000}`, type: 'nope'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'},{id: `${Math.random() * 100000000}`, type: 'nope'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'},{id: `${Math.random() * 100000000}`, type: 'nope'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'},{id: `${Math.random() * 100000000}`, type: 'nope'}, {id: `${Math.random() * 100000000}`, type: 'tacocat'}, ],
    selectedCards: [],
    graveyardCards: [],
    gameEvent: "",
    cardsInDeck: 0,
    nextCardInDeck: null
  },
  reducers: {
    ////////////////////////////////////////////////////////
    //                   My Card Actions                  //
    ////////////////////////////////////////////////////////
    setCards: (state, action) => {
      state.myCards = [...action.payload];
    },
    addCard: (state, action) => {
      state.myCards = [...state.myCards, action.payload];
    },
    removeCard: (state, action) => {
      state.myCards = state.myCards.filter((card) => {
        return card.id !== action.payload;
      });
      state.selectedCards = state.selectedCards.filter((card) => {
        return card.id !== action.payload;
      });
    },
    clearSelectedCards: (state) => {
      state.selectedCards = [];
    },
    selectCard: (state, action) => {
      state.selectedCards = [...state.selectedCards, action.payload];
    },
    deselectCard: (state, action) => {
      state.selectedCards = state.selectedCards.filter((card) => {
        return card !== action.payload;
      });
    },
    ////////////////////////////////////////////////////////
    //                 Deck Cards Actions                 //
    ////////////////////////////////////////////////////////
    setDeck: (state, action) => {
      state.cardsInDeck = action.payload;
    },
    setNextCardInDeck: (state, action) => {
      state.nextCardInDeck = action.payload;
    },
    ////////////////////////////////////////////////////////
    //             Graveyard Cards Actions                //
    ////////////////////////////////////////////////////////
    setGraveYard: (state, action) => {
      state.graveyardCards = [...action.payload];
    },
    ////////////////////////////////////////////////////////
    //                   Game Events                      //
    ////////////////////////////////////////////////////////
    setGameEvent: (state, action) => {
      state.gameEvent = action.payload;
    },
  },
});

export const {
  addCard,
  selectCard,
  deselectCard,
  removeCard,
  clearSelectedCards,
  setGameEvent,
  setCards,
  setDeck,
  setGraveYard,
  setNextCardInDeck,
} = GameStateSlice.actions;

export const selectMyCards = (state: RootState) =>
  state.GameStateReducers.myCards;
export const selectSelectedCards = (state: RootState) =>
  state.GameStateReducers.selectedCards;
export const selectDeck = (state: RootState) =>
  state.GameStateReducers.cardsInDeck;
export const selectGraveyard = (state: RootState) =>
  state.GameStateReducers.graveyardCards;
export const selectGameEvents = (state: RootState) =>
  state.GameStateReducers.gameEvent;
export const selectNextCardInDeck = (state: RootState) =>
  state.GameStateReducers.nextCardInDeck;

export default GameStateSlice.reducer;
