import { Card, InHandCard } from "./Card";
import TacocatImage from "../../assets/tacocat.webp";
import TacoCatIcon from "../../assets/card-icon.svg";
import NopeIcon from "../../assets/nope.svg";


const CardFactory = (id: string, type: string) => {
  switch (type) {
    case "tacocat":
      return (
        <Card
          cardName="TACOCAT"
          cardBorderColour="grey"
          cardImage={TacocatImage}
          cardIcon={TacoCatIcon}
          cardFlavourText={null}
          cardEffectText={null}
        />
      );
    case "nope":
      return (
        <Card
          cardName="NOPE"
          cardBorderColour="grey"
          cardImage={NopeIcon}
          cardIcon={NopeIcon}
          cardFlavourText={null}
          cardEffectText={null}
        />
      );
  }

  return (
    <Card
          cardName="ERROR"
          cardBorderColour="grey"
          cardImage={NopeIcon}
          cardIcon={NopeIcon}
          cardFlavourText={null}
          cardEffectText={null}
        />
  );
};


export { CardFactory };
